import Vue from 'vue'
import Router from 'vue-router'

import Students from '@/components/Students'
import Teachers from '@/components/Teachers'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/students/联考1班'
    },
    {
      path: '/students/:grade',
      component: Students
    },
    {
      path: '/teachers',
      component: Teachers
    },
  ]
})



