import Vue from 'vue'
import iView from 'iview'
import router from './router'
import App from '@/components/App'
import 'iview/dist/styles/iview.css'

Vue.config.productionTip = false

Vue.use(iView)

new Vue({
  el: '#root',
  router,
  components: {App},
  template: '<App/>'
})



