import AV from './AV'

export function get_students(grade) {
  const sts = new AV.Query('_User')
  const reg = new RegExp(`-${grade}$`, 'i')
  return sts.matches('username', reg).find().then(sts => sts.map(s => st_to_json(s)))
}

export function get_teachers() {
  const teachers = new AV.Query('_User')
  return teachers.exists('subject').find().then(teachers => teachers.map(t => teacher_to_json(t)))
}

function st_to_json(s) {
  return {
    id: s.id,
    name: s.get('username').replace(/-.+$/, ''),
    self: transform(s.get('self')),
    dad: transform(s.get('dad')),
    mom: transform(s.get('mom'))
  }
}

function teacher_to_json(t) {
  return {
    id: t.id,
    name: t.get('username'),
    subject: t.get('subject'),
    result: transform(t.get('self'))
  }
}

export function transform(data) {
  if(data.length < 1) return '还没测'

  let arr = [0, 0, 0, 0, 0, 0, 0, 0, 0]
  data.forEach(item => {if(item >=1 && item <= 9) arr[item - 1] += 1})

  const max = Math.max(...arr)
  console.log(max);
  if(max <= 0) return '还没测'

  let results = []
  arr.forEach((value, index) => {
    if(value >= max) {
      results.push(index + 1)
    }
  })
  return results.join('+')
}



