<template lang="pug">
div
  h1 {{$route.params.grade}}
  Table.table(:columns="columns" :data="students" ref="sts" stripe)
  .right
    Button(type="primary" v-if="students.length > 0" @click="$refs.sts.exportCsv({filename: $route.params.grade})") 导出CSV
</template>

<script>
import {get_students} from '@/api/users'

export default {
  data() {
    return {
      columns: [
        {title: '姓名', key: 'name'},
        {title: '本人', key: 'self'},
        {title: '父亲', key: 'dad'},
        {title: '母亲', key: 'mom'},
      ],
      students: []
    }
  },
  created() {
    this.get_students()
  },
  watch: {
    '$route': 'get_students'
  },
  methods: {
    async get_students() {
      const grade = this.$route.params.grade
      this.students = await get_students(grade)
    }
  }
}
</script>

<style lang="css" scoped>
h1 {
  font-size: 20px;
}
.table {
  margin: 16px 0;
}
</style>



