<template lang="pug">
div
  h1 教师列表
  Table.table(:columns="columns" :data="teachers" ref="teachers" stripe)
  .right
    Button(type="primary" v-if="teachers.length > 0" @click="$refs.teachers.exportCsv({filename: '教师列表'})") 导出CSV
</template>

<script>
import {get_teachers} from '@/api/users'

export default {
  data() {
    return {
      columns: [
        {title: '姓名', key: 'name'},
        {title: '科目', key: 'subject'},
        {title: '类型', key: 'result'},
      ],
      teachers: []
    }
  },
  created() {
    this.get_teachers()
  },
  watch: {
    '$route': 'get_teachers'
  },
  methods: {
    async get_teachers() {
      this.teachers = await get_teachers()
    }
  }
}
</script>

<style lang="css" scoped>
h1 {
  font-size: 20px;
}
.table {
  margin: 16px 0;
}
</style>



